        data-backdrop="static"
        data-keyboard="false"
        tabindex="-1"
        role="dialog"
        aria-labelledby="staticBackdropLabel"
        aria-hidden="true"
      >
        <div class="modal-dialog">
          <div class="modal-content">
            <div class="modal-header position-relative">
              <button
                type="button"
                class="close position-absolute"
                data-dismiss="modal"
                aria-label="Close"
              >
                <span aria-hidden="true">&times;</span>
              </button>
            </div>
            <div class="modal-body">
              <detail class="det"></detail>
            </div>
          </div>
        </div>
      </div>
      <!--  -->

      <div class="header">
        <div class="header_p">
          <p class="header_p1">{{ keyValue }}</p>
          <!-- <p class="header_p2">
          从野兽派建筑到极简主义建筑，这个Unsplash
          Awards类别展示了建筑艺术，试图理解和反思塑造一个地方叙事的建筑。
        </p> -->
        </div>

        <!-- <button class="anniu">提交给建筑</button> -->
        <div class="index">
          <div class="index_yilou" :key="index" v-for="(item, index) in list">
            <div>
              <a data-toggle="modal" data-target="#staticBackdrop"
                ><img :src="item.image" alt=""
              /></a>
            </div>
            <div class="index_yilou_d1">
              <div class="index_yilou_d1_1">
                <a href="/comm">{{ item.subject }}</a>
              </div>
              <div class="index_yilou_d1_2">{{ item.fenlei }}</div>
              <div class="index_yilou_d1_3">
                <a href="/comm">
                  <div class="iconfont icon-guankan1">{{ item.views }}}</div>
                  <div class="iconfont icon-pinglun1">{{ item.comment }}</div>
                  <div class="iconfont icon-zan">{{ item.xihuan }}</div>
                </a>
              </div>
            </div>
            <div class="index_yilou_d1_4">
              <img :src="item.avata" alt="" />
              <a href="">{{ item.uname }}</a>
              <span>{{ item.time }}</span>
            </div>
          </div>
        </div>
        <!-- <div class="f">
          <div class="fenye1"><a href="">1</a></div>
          <div class="fenye2"><a href="">2</a></div>
