</template>

<script>
import Detail from "../../views/Detail.vue";

export default {
  props: ["keyValue"],
  data() {
    return {
      list: [],
    };
  },
  methods: {
    // 去重
    delrep(arr) {
      return arr.reduce((p, v) => {
        let _s = true;
        p.forEach((element) => {
          if (element.id == v.id) {
            _s = false;
          }
        });
        _s ? (p = p.concat(v)) : "";
        return p;
      }, []);
    },
  },
  watch: {
    async keyValue() {
      let _res = await this.axios.get(`/search/`, {
        params: { key: this.keyValue },
      });
      this.list =this.delrep(_res.data.data);
      console.log(this.list);
    },
  },
  async mounted() {
    let _res = await this.axios.get(`/search/`, {
      params: { key: this.keyValue },
    });
    this.list =this.delrep(_res.data.data);
    console.log(this.list);
  },
  components: {
    Detail,
  },
  //  beforeRouteUpdate(to,from,next){
  //   console.log(to.params.keyValue)
  // },
};
</script>>

