<script>
import bgimg from "../assets/register/stones.jpg";
export default {
  data() {
    return {
      bgimg: bgimg,
      // 用户名密码样式
      name1: "uname",
      name2: "",
      pwd1: "upwd",
      pwd2: "",
      uemail1: "uemail",
      uemail2: "",
      // 用户名
      username: "",
      // 密码
      upwd: "",
      email: "",
      // 用户名重复
      ploy: false,
      regname: false,
      ppp: 0,
    };
  },
  methods: {
    regName() {
      let nameReg = /^[0-9a-zA-z_]{6,18}$/;
      if (nameReg.test(this.username)) {
        (this.name1 = "uname"), (this.name2 = "");

        // 判断是否重复
        this.axios.post("/regName", "username=" + this.username).then((res) => {
          let data = res.data.code;
          // console.log(data);
          if (data == 3000) {
            this.ploy = true;
            (this.name1 = "uname noname"), (this.name2 = "noname");
            this.ppp = 0;
          } else {
            this.ploy = false;
            (this.name1 = "uname"), (this.name2 = "");
            this.ppp = 1;
          }
        });
        this.regname = true;
      } else {
        (this.name1 = "uname noname"), (this.name2 = "noname");
        this.regname = false;
      }
    },
    regPwd() {
      let upwdReg = /^[0-9a-zA-z]{6,18}$/;
      if (upwdReg.test(this.upwd)) {
        (this.pwd1 = "upwd"), (this.pwd2 = "");

        return true;
      } else {
        (this.pwd1 = "upwd nopwd"), (this.pwd2 = "nopwd");

        return false;
      }
    },
    regEmail() {
      //邮箱验证
      let emailReg = /^[a-zA-Z0-9_-]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/;
      if (emailReg.test(this.email)) {
        this.uemail1 = "uemail";
        this.uemail2 = "";

        return true;
      } else {
        this.uemail1 = "uemail noemail";
        this.uemail2 = "noemail";
        return false;
      }
    },
    reg() {
      if (this.ppp == 1 && this.regname && this.regPwd() && this.regEmail()) {
        // 发送邮件
        this.axios.get(
          "/send?uemail=" +
            this.email +
            "&uname=" +
            this.username +
            "&upwd=" +
            this.upwd
        );
        window.alert("邮件已发送到您的邮箱，请前去激活。");
        // console.log("1");
      } else {
        window.alert("请填写正确的信息");
      }
    },
  },
};
</script>